

.page-text-div {
    display: flex;
    flex-direction: column;
    text-align: left;
    
    /* justify-content: center; */
    color: var(--text);
    /* min-width: 280px; */
    width: max(80%, 280px);
}

/* style={{display: 'flex', flexWrap: 'wrap', maxWidth: 'fit-content', color: 'var(--text)', textAlign: 'left'}} */

.page-text-div-title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    color: var(--text);
    margin-bottom: 50px;
}





.center-text {
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

.outer-div {
    display: flex;
    text-align: center;
    flex-direction: column;
    /* height: 100vh; */
    /*background-color: var(--testing);*/
}

.inner-div {
    display: flex;
    flex-direction: column;
    /* flex-wrap: wrap; */
    padding-left: 20px;
    padding-right: 20px; 
    /* z-index: 3; */
}

.login-div {
    display: flex; 
    flex-direction: column; 
    align-items: center;
}

.login-signup {
    cursor: pointer;
    width: 240px;
    height: 80px;
    /* font-family: 'Courier New', Courier, monospace; */
    font-size: 32px;
    font-weight: bold;
    margin-top: 0px;
    border-radius: 8px;
    border: 0px;
    background-color: var(--button);
    color: var(--headerText);

}

.login-signup:hover {
    background-color: var(--buttonHover);
}

.login-signin {
    cursor: pointer;
    width: 100px; 
    height: 40px;
    /* font-family: 'Courier New', Courier, monospace; */
    font-size: 24px;
    font-weight: bold;
    margin-top: 40px;
    border: 0;
    background-color: #0000;
    color: var(--text);
}

.login-signin:hover {
    color: var(--textHover);
}