.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.non-selectable {
  -webkit-user-select: none;
  /* Safari */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* IE10+/Edge */
  user-select: none;
  /* Standard */
}

.background {
  background-color: var(--background1);
  transition: all 0.5s;
}

.background-img {
  position: absolute;
  width: 100%;
  z-index: -1;
}

.background-img-light {
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
}

.background-img-dark {
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
}

.hamburger-button {
  cursor: pointer;
  background-color: var(--headerBackground);
  width: 40px;
  height: 40px;
  margin-left: 10px;
  margin-right: 0px;
  border: none;
}

.hamburger-button>div>div {
  background-color: var(--hamburgerBar);
  width: 24px;
  height: 3px;
  border-radius: 20px;
}

.hamburger-button:hover>div>div {
  transition: 0.3s;
  background-color: var(--hamburgerBarHover);
}

.hamburger-button:active>div>div {
  transition: 0.3s;
  background-color: var(--hamburgerBarActive);
}



.footer-text-selected {
  color: var(--text);
  transition: 0.3s;
  cursor: pointer;
  background-color: #0000;
  border-color: #0000;
  color: var(--footercurrentpagetext);
  outline: 0;
  text-decoration: none;
  /* font-family: 'Courier New', Courier, monospace; */
  font-size: 12px;
  margin-right: 16px;
  margin-left: 16px;
  margin-top: 3px;
}

.footerText {
  color: var(--text);
  transition: 0.3s;
  cursor: pointer;
  background-color: #0000;
  border-color: #0000;
  outline: 0;
  text-decoration: none;
  /* font-family: 'Courier New', Courier, monospace; */
  font-size: 12px;
  margin-right: 16px;
  margin-left: 16px;
  margin-top: 3px;
}

.footerText:hover {
  color: #888;
}

.footerDivider {
  /* font-family: 'Courier New', Courier, monospace; */
  font-size: 12px;
  color: #BBB;
  margin: 0px;
  margin-top: 2px;
}

.footerNavOptions {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: row;
  width: 100%;
}

.footerCopyright {
  display: flex;
  justify-content: center;
  flex-direction: row;
  width: 100%;
}

.footer-copyright-container {
  display: flex;
  justify-content: center;
  flex-direction: row;
  width: 100%;
}

.footer-copyright-text {
  /* font-family: 'Courier New', Courier, monospace; */
  font-size: 10px;
  color: #000;
  margin-bottom: 20px;
}

.cookieButton {
  background: #0080ee none repeat scroll 0% 0%;
  background-color: var(--button);
  border: 0px none;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  height: 26px;
  width: 100px;
  align-self: center;
  justify-self: center;
  margin-right: 20px;
  font-size: 13px;
  position: absolute;
  bottom: 20px;
  left: 150px;
  z-index: 8;
}

.cookieButton:hover {
  transition: 0.3s;
  background-color: var(--buttonHover);
}

.portfolio-img {
  background: linear-gradient(#444 0%, #AAA 100%);
}

.portfolio-img-button {
  all: unset;
  transition: 0.3s;
  cursor: pointer;
  z-index: 0;
}

.portfolio-img-button:hover {
  transform: scale(1.02);
  z-index: 1;
}

.text {
  /* font-family: 'Courier New', Courier, monospace; */
  color: var(--text);
}



.text-fade-in {
  /* font-family: 'Courier New', Courier, monospace; */
  color: var(--text);
  -webkit-animation: fadein 2s;
  /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 2s;
  /* Firefox < 16 */
  -ms-animation: fadein 2s;
  /* Internet Explorer */
  -o-animation: fadein 2s;
  /* Opera < 12.1 */
  animation: fadein 2s;
}

.document-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  box-shadow: 6px 6px 16px #0004;
  color: #000;
  background-color: #fff;
}

.document-center-text {
  text-align: center;
}

.document-text-left {
  text-align: left;
}

.doc-bold {
  font-weight: bold;
}

.doc-italic {
  font-style: italic;
}

.doc-text {
  font-size: clamp(1rem, 1.5vw, 1.25rem);
}

.doc-divider-line {
  height: 2px;
  background-color: #000;
  width: 100%;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.spantext {
  color: var(--spantext);
}

.text-scalable-1 {
  /* font-family: 'Courier New', Courier, monospace; */
  font-size: clamp(1rem, 1.5vw, 1.25rem);
}

.text-scalable-2 {
  /* font-family: 'Courier New', Courier, monospace; */
  font-size: clamp(2rem, 3.0vw, 2.5rem);
}

.lucida-console {
  font-family: "Lucida Console";
}

.fade-in-xl {
  /* font-family: 'Courier New', Courier, monospace; */
  color: var(--text);
  font-size: calc(4vw + 20px);
  -webkit-animation: fadein 2s;
  /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 2s;
  /* Firefox < 16 */
  -ms-animation: fadein 2s;
  /* Internet Explorer */
  -o-animation: fadein 2s;
  /* Opera < 12.1 */
  animation: fadein 2s;
}

.divider-line {
  transition: 0.3s;
  width: calc(100% - 40px);
  height: 3px;
  margin: 20px;
  background-color: var(--divider);
}


@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Internet Explorer */
@-ms-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.button:hover {
  background: rgb(255, 230, 120) none repeat scroll 0% 0%;
}

.a-list-button-link {
  text-align: left;
  vertical-align: top;
  border-style: none;
  font-size: inherit;
  text-decoration: underline;
  background: none;
  cursor: pointer;
  color: var(--aLink);
}

.a-list-button-link:hover {
  transition: 0.3s;
  color: var(--aLinkHover);
}

.text-color {
  color: var(--text);
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}


/* style={{ backgroundColor: '#0000', display: 'flex', flexDirection: 'column', alignItems: 'center', width: 128, height: 128 + 60, padding: 10, margin: 10 }} */

.img-text-button-container {
  background-color: #0000;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* width: 96px;
  height: 156px; */
  width: 112px;
  height: 172px;
  /* width: 128px;
  height: 188px; */
  padding: 10px;
  margin: 10px;
}

.img-text-button {
  all: unset;
  position: relative;
  background-color: #aaa4;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 112px;
  height: 172px;
  padding: 10px;
  margin: 10px;
  transition: 0.5s;
  cursor: pointer;
  top: 0px;
}

.border {
  border-style: solid;
  border-color: #888;
  border-width: 2px;
}

.img-text-button:hover {
  top: -16px;
  /* width: 128px;
  height: 188px; */
  width: 144px;
  height: 204px;
  /* width: 160px;
  height: 220px; */
}

.download-button {
  all: unset;
  border-radius: 4px;
  border-style: solid;
  border-color: #8884;
  border-width: 1px;
  margin: 10px;
  padding: 4px;
  font-size: 14px;
  font-weight: bold;
  color: #000;
  background-color: #fffc;
  box-shadow: #0004 1px 1px 4px;
  transition: 0.5s;
  cursor: pointer;
}

.download-button:hover {
  background-color: #cccc;
}

.clipart-results-container {
  display: flex;
  flex-wrap: wrap;
  margin: 10px;
  justify-content: center;
}

.svg-source {
  background-color: var(--background3);
  /* border-color: var(--background2);
  border-style: solid;
  border-width: 1px; */
  overflow: scroll;
  flex-wrap: wrap;
  padding: 10px;
  /* margin: 20px; */
  margin-top: 0px;
}

.co {
  cursor: crosshair;
}


.content-header-container {
  border-style: solid;
  border-color: #888;
  border-width: 0px;
  border-bottom-width: 1px;
  background-color: #8884;
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 20px;
  padding-top: 10px;
  margin-bottom: 0px;
}

.content-header-text {
  text-align: left;
  margin: 10px;
  margin-top: 0px;
}


.loader {
  border: 10px solid #8bf4; /* grey blue */
  border-top: 10px solid #2af; /* Blue */
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}