.header-shadow {
    display: flex;
    height: 12px;
    background: var(--headerGradient);
}

.header-dropdown-click-outside {
    all: unset;
    display: flex;
    width: 100vw;
    height: calc(100vh - 152px);
    background-color: #0004;
}

.header-button {
    border-radius: 5px;
    border-style: none;
    background-color: #385;
    width: 128px;
    height: 48px;
    align-self: center;
    margin-top: 8px;
    color: #FFF;
    font-size: 22px;
}

.header-link-b {
    all: unset;
    margin: 0px;
    margin-top: 8px;
    padding: 0px;
    font-family: Arial;
    font-size: 16px;
    text-decoration: none;
    font-weight: bold;
    margin-left: 24px;
    color: var(--headerText);
    cursor: pointer;
}

.header-link-b:hover {
    transition: 0.3s;
    opacity: 80%;
}

.header-link-b:active {
    transition: 0.3s;
    opacity: 60%;
}


.header-link {
    font-family: Arial;
    font-size: 16px;
    text-decoration: none;
    font-weight: bold;
    margin-left: 24px;
    margin-top: 18px;
    /* margin-bottom: 12px; */
    color: var(--headerText);
}

.header-link:hover {
    transition: 0.3s;
    opacity: 80%;
}

.header-link:active {
    transition: 0.3s;
    opacity: 60%;
}

.header-link-disabled {
    font-family: Arial;
    font-size: 16px;
    text-decoration: none;
    font-weight: bold;
    margin-left: 24px;
    margin-top: 18px;
    /* margin-bottom: 12px; */
    color: var(--headercurrentpagetext);
}

.header-hamburger-menu {
    display: flex;
    flex-direction: row;
    background-color: var(--headerBackground);
    padding-bottom: 20px;
    border-style: solid;
    border-bottom-width: 0px;
    border-right-width: 0px;
    border-left-width: 0px;
    border-top-width: 1px;
    border-color: #444;
    border-top-color: #333;

}

.header-hamburger-menu-link-container {
    display: flex;
    flex: 1;
    flex-direction: column;
}

.search-text {
    /* all: unset; */
    border-style: none;
    /* max-width: 200px; */
    /* width: 120px; */
    height: 32px;
    margin: 6px 0px 0px 10px;
    border-radius: 4px 0px 0px 4px;
    /* border-radius: 2px; */
}

.search-text:focus {
    outline:solid 1px #000;
    /* border-style: solid;
    border-color: #0004;
    border-width:8px; */
}

.search-button {
    /* all: unset; */
    cursor: pointer;
    border-style: none;
    width: 34px;
    height: 34px;
    margin: 6px 20px 0px 0px;
    border-radius: 0px 4px 4px 0px;
    background-color: #4d08;
    transition: 0.3s;
}

.search-button:hover {
    background-color: #8f4a;
}

.search-button:active {
    opacity: 0.75;
}




















.topnav {
    overflow: hidden;
    background-color: #e9e9e9;
  }
  
  .topnav a {
    float: left;
    display: block;
    color: black;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
  }
  
  .topnav a:hover {
    background-color: #ddd;
    color: black;
  }
  
  .topnav a.active {
    background-color: #2196F3;
    color: white;
  }
  
  .topnav .search-container {
    float: right;
  }
  
  .topnav input[type=text] {
    padding: 6px;
    margin-top: 8px;
    font-size: 17px;
    border: none;
  }
  
  .topnav .search-container button {
    float: right;
    padding: 6px 10px;
    margin-top: 8px;
    margin-right: 16px;
    background: #ddd;
    font-size: 17px;
    border: none;
    cursor: pointer;
  }
  
  .topnav .search-container button:hover {
    background: #ccc;
  }
  
  @media screen and (max-width: 600px) {
    .topnav .search-container {
      float: none;
    }
    .topnav a, .topnav input[type=text], .topnav .search-container button {
      float: none;
      display: block;
      text-align: left;
      width: 100%;
      margin: 0;
      padding: 14px;
    }
    .topnav input[type=text] {
      border: 1px solid #ccc;  
    }
  }

